body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  width:95%;
  text-align: center;
  margin: auto;
}

*{
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}
.cardView{
    display: flex;
    flex-wrap: wrap;
   justify-content: center;
   flex-basis:auto;
   width: 95%;
   margin: 15px auto;
   padding: 3px;
   border-radius: 3px;
}

.card{
    width: 32%;
    margin:5px;
    border: 1px solid #eeeeee;
    padding: .5%;
   box-shadow: 1px 1px 1px 1px rgba(0,0,0,0.2) !important;
   border-radius: 3px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   cursor: pointer;
}
.card__title{
    text-align: left;
    text-decoration: underline;
    color: #003580;
    padding: 15px 0px;
    font-weight: bold;
}
.card__body{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.card__image{
    border-radius: 12px;
    border: 1px solid #eeeeee;
    max-width: 90px;
    margin: 5px;
    border: 1px solid #eeeeee;
    padding: 1.1%;
    box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2) !important;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
}
.card__info{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: baseline;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400 !important;
}

.card__info__price{
    color: green;
    font-weight: bold;

}
.card__info__rate{
padding: 10px 0px;
color: #003580;
font-weight: bold;
}
.card__info__review{
    color: grey;
    font-weight: bold;

}


@media screen and (max-width: 768px) {
    .cardView{width: 100%};
    .card{ width: 40%; margin:2px;padding: 1px;}
    .card__title{padding: 10px 0px;font-weight: 600;}
    .card__image{width:100%; box-shadow: 1px 1px 1px 1px rgba(0,0,0,0.1)}
    .card__info__rate{font-size: small;}

  }

.View__body {
  max-width: 92%;
  margin: auto;
  border: 1px solid #eeeeee;
  padding: 3px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2) !important;
  border-radius: 3px;
}
.view__body__title {
  text-align: left;
  margin: 15px 10px;
  color: #003580;
}
.view__body__night__control {
  text-align: left;
  margin: 15px;
  padding: 10px;
}
.view__body__night__control span {
  text-align: left;
  padding: 5px;
  font-weight: 600;
  color: rgb(146, 43, 43);
}

.view__body__slider {
  overflow: hidden;
  margin: auto;
  max-width: 45%;
}

.container {
  position: relative;
  display: inline-block;
  margin: 1rem;
}

.container:hover {
  border: 1px solid darkgray;
  border-radius: 0.5rem;
}

input[type="number"] {
  position: relative;
  padding: 4px;
  padding-right: 9px;
  width: 60px;
  text-align: center;
  font-size: unset;
  font-weight: 500;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  opacity: 1;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: inner-spin-button !important;
  width: 15px;
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
}


@media screen and (max-width: 768px) {
  .view__body__slider{min-width: 85%};
}


.slide__photo__big {
  max-width: 70%;
  margin: 20px auto;
}
.slide__photo__big img {
  width: 100%;
  height: 100%;
  border: 10px solid;
  padding: 10px;
}

.slide__photo__range {
  display: flex;
  justify-content: baseline;
}

.slide__photo__range input {
  width: 50%;
}
.slide__photo__range button {
  margin: 2px;
  border: none;
  cursor: pointer;
}

input[type="range"] {
  height: -1px;
  -webkit-appearance: none;
  margin: 10px 0;
  width: 100%;
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 12px;
  cursor: pointer;
  -webkit-transition: 550ms ease-in-out;
  transition: 550ms ease-in-out;
  box-shadow: 1px 1px 0px #bcc8d9;
  background: rgb(30, 162, 223);
  border-radius: 50px;
  border: 1px solid rgb(11, 121, 172);
}

input[type="range"]::-webkit-slider-thumb {
  border: 1px solid rgb(66, 147, 194);
  height: 20px;
  width: 20px;
  border-radius: 7px;
  background: rgb(103, 162, 196);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6px;
}

.slide__photo__range__btn {
  display: block;
  padding: 0.5rem 0.75rem;
  margin-left: -1px;
  line-height: 1.25;
  color: rgb(66, 120, 151);
  background-color: #fff;
  border: 1px solid #dee2e6;
  cursor: pointer;
  outline-style: none;
  border-radius: 5px;
  font-weight: 600;
  font-size: 19px;
  align-self: flex-end;
}

.slide__photo__range__btn:hover {
  color: #0056b3;
  text-decoration: none;
}
.slide__photo__range__btn:disabled {
  color: rgb(118, 167, 195);
  text-decoration: none;
}

.slide__photo__thumbnail {
  position: relative;
  margin: 0px auto;
  height: 80px;
  width: 100px;
  outline: dashed;
}

@media screen and (max-width: 768px) {
  .slide__photo__thumbnail{display: none};
}

.slider-wraper {
  display: flex;
  position: absolute;
  height: 80px;
  -webkit-transition: 550ms ease-in-out;
  transition: 550ms ease-in-out;
}
.slider__img {
  flex: 1 1;
  opacity: 0.5;
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
  -webkit-transition: 500ms ease-in-out;
  transition: 500ms ease-in-out;
}
